const create =  async (newObject, token) => {
  await fetch('https://mi-folio-app.herokuapp.com/api/add-project', {
    method: 'POST',
    headers: {
      'Authorization': `Bearer ${token}`
    },
    body: newObject
  }).then(res => res.json())
    .then(data => {
      return data
    })
}

// eslint-disable-next-line import/no-anonymous-default-export
export default {
  create
};
